/*-------------- Main styles --------------*/
:root {
  --radius: 3px;
  --blue: #5a6b88;
  --orange: #f19c44;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  color: #555;
}

h1, h2, h3, h4, h5, h6 {
  color: #777;
}

h1 {
  font-size: 24px;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}
.container-flex {
  display: flex;
  justify-content: space-between;
}

/* ----------- Pages ----------------- */
.posts, .about {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: auto;
}

.about {
  padding: 10px;
}

.about__nav {
  /*padding: 0 20px;*/
}
.about__title {
  color: #fff;
  text-align: start;
}
.about__list {
  list-style: inside;
  padding: 10px 0;
}

/*--------------- Header --------------*/
.post-header__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  min-height: 60px;
}
.post-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
  background-color: #5a6b88;
  background-color: var(--blue);
  border-bottom-left-radius: 3px;
  border-bottom-left-radius: var(--radius);
  border-bottom-right-radius: 3px;
  border-bottom-right-radius: var(--radius);
}
.post-header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* nav-item */
.nav-item {
  display: flex;
  width: 33.33%;
}
.nav-item button {
  border: 1px solid #fff;
  color: #fff;
}
.nav-item button:hover {
  background-color: #f19c44;
  background-color: var(--orange);
  border-color: #f19c44;
  border-color: var(--orange);
}
.post-header__title {
  /*flex-grow: 1;*/
  color: #fff;
  text-align: center;
}

.add-btn {
  margin: 10px 0;
}

/*-------------- Post form --------------*/
.post-form {
  padding: 10px 0;
}
.post-form__title {
  padding-bottom: 20px;
  text-align: center;
}
.post-form__input {
  padding-top: 10px;
  padding-bottom: 10px;
}
.post-form__btns {
  display: flex;
  justify-content: flex-end;
  padding: 15px 0 0;
}

/* ------------ Login -------------------- */
.login__title {
  text-align: center;
  color: #fff;
}
.login-form {
  width: 50%;
  margin: 0 auto;
  padding: 30px 10px;
  box-shadow: 0 0 3px #5a6b88;
  box-shadow: 0 0 3px var(--blue);
}

.logout__btn {
  margin: 0 10px;
  border: 1px solid #fff;
  border-radius: 3px;
  border-radius: var(--radius);
  color: #fff;
}
.logout__btn:hover {
  background-color: #f19c44;
  background-color: var(--orange);
  border-color: #f19c44;
  border-color: var(--orange);
}

/*----------------- Post list --------------*/
.post-list {
  flex-grow: 1;
  padding: 60px 0;
}

.post-list__loader {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.post-list__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.post-list__title {
  margin: 10px 0;
  text-align: center;
}

/*--------------- Post item -----------------*/
.post {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  padding: 15px;
  /*border: 1px solid var(--blue);*/
  border-radius: 3px;
  border-radius: var(--radius);
  box-shadow: 0 0 3px #5a6b88;
  box-shadow: 0 0 3px var(--blue);
}
.post:hover {
  box-shadow: 0 0 3px #f19c44;
  box-shadow: 0 0 3px var(--orange);
}
.post__content {
  padding-right: 10px;
  cursor: pointer;
  transition: 0.3s;
}

.post-enter {
  transform: translateX(-300px);
}
.post-enter-active {
  transform: translateX(0);
  transition: all 300ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  opacity: 0;
  transition: opacity 300ms ease-in;
}

.post__btns {
  flex-shrink: 0;
}


/* ------------------------ Footer ---------------------- */
.post-footer {
  width: 100%;
  background-color: #5a6b88;
  background-color: var(--blue);
}
.post-footer p {
  text-align: center;
  color: #bbb;
  font-size: 14px;
  padding: 10px;
}
.post-footer p a {
  color: #45b29a;
  transition: 0.5s;
  text-decoration: underline;
}
.post-footer p a:hover {
  color: #f19c44;
  color: var(--orange);
  text-shadow: 0 0 2px #f19c44, 0 0 10px #f19c44;
  text-shadow: 0 0 2px var(--orange), 0 0 10px var(--orange);
}


/* ------------- Responsive -------------- */
@media (max-width: 900px) {
  .post-list {
    padding: 60px 10px 10px;
  }
}

@media (max-width: 720px) {
  .about__nav {
    padding: 0 20px 0 10px;
  }
  .about__title {
    text-align: end;
  }

  .login-form {
    width: 100%;
  }

  .post-header__nav {
    flex-wrap: wrap;
  }

  .nav-item {
    flex-grow: 1;
    width: 50%;
  }
  .nav-item:nth-child(1) {
    order: 1;
  }
  .nav-item button {
    width: 50%;
    margin-top: 5px;
    padding: 7px 0;
  }

  .post-list {
    padding: 100px 10px 10px;
  }

  .about.post-list {
    padding: 60px 10px 10px;
  }

  .post {
    flex-direction: column;
    align-items: flex-start;
  }

  .post__btns button {
    margin-top: 10px;
    padding: 10px 5px;
  }
}

@media (max-width: 380px) {
  .pagination__btn {
    margin-right: 5px;
  }
}

