.basicModal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
}

.basicModalContent {
  padding: 20px;
  background-color: #fff;
  border-radius: var(--radius);
  min-width: 250px;
}

.basicModal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 720px) {
  .basicModalContent {
    width: 90%;
  }
}
